<template>
  <div>
    <Header></Header>
    <h1>{{this.account.fullName}}</h1>
    
    <form @submit.prevent v-if="isAdd">
      <div>
        <h1>Ghi Chú</h1>
        <input type="text" v-model="account.msg" />
        <br />
        <button @click="onSubmit(account)">Thêm</button>
        <button @click="isAdd=false">Hủy</button>
      </div>
    </form>
    <div v-else>
      <button @click="isAdd=true">Thêm ghi chú</button>
      <ul>
        <li v-for="(todo,index) in arr" :key="index">
          <button @click="onRemove(todo)">&#128219;</button>
          <span>{{todo}}</span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import Todo from "../requester/todo";
import Header from "../components/Header";

const app = document.querySelector("#app");

export default {
  data() {
    return {
      isAdd: false,
      account: {},
      arr: [],
      isLogin: localStorage.getItem("auth") ? true : false,
    };
  },
  components: {
    Header,
  },
  methods: {
    onLoad(isLoad) {
      isLoad ? app.classList.add("loadding") : app.classList.remove("loadding");
    },
    onSubmit(account) {
      if (this.isLogin) {
        this.onLoad(true);
        // add Message
        Todo.addMsg(this.account)
          .then((res) => {
            const { result } = res.data;

            if (result === "error") {
              localStorage.removeItem("auth");
              this.$router.push({ path: "/login" });
              return;
            }
            this.arr.push(account.msg);
            this.onFomat();
            this.onLoad(false);
          })
          .catch((err) => console.log(err));
      } else {
        this.$router.push({ path: "/login" });
      }
    },
    onRemove(todo) {
      this.onLoad(true);
      const { _id } = this.account;
      const msg = todo.trim();
      // remove Message
      Todo.removeMsg({ _id, msg })
        .then((res) => {
          const { result } = res.data;
          if (result === "success") {
            let index = this.arr.findIndex((i) => i == todo);
            this.arr.splice(index, 1);
          }
          this.onLoad(false);
        })
        .catch((err) => console.log(err));
    },

    onFomat() {
      this.isAdd = false;
      this.account.msg = "";
    },
  },
  created() {
    if (this.isLogin) {
      this.onLoad(true);
      // get Message
      Todo.get()
        .then((dt) => {
          const { result } = dt.data;

          if (result === "success") {
            const { info } = dt.data; // {_id,fullName,msg}
            this.arr = info.msg;
            delete info.msg;
            this.account = info;
            console.log(this.account)
          }
          if (result === "error") {
            localStorage.removeItem("auth");
          }
          this.onLoad(false);
        })
        .catch((err) => {
          this.onLoad(false);
          console.log(err);
          localStorage.removeItem("auth");
        });
    } else {
      this.$router.push({ path: "/login" });
    }
  },
};
</script>

<style scoped>
li,
span {
  transition: all 0.5s;
}

form div {
  margin: auto;
}

ul {
  margin: 1rem auto 0;
  padding: 10px 0;
  width: 100%;
  max-width: 800px;
  text-align: left;
  box-shadow: 0 0 5px #08663c;
  background: #282d2b30;
  border-radius: 15px;
}
input {
  padding: 5px 10px;
  margin: 0.5rem 0;
  border-radius: 4px;
  font-size: 0.8rem;
  border: thin solid #296047;
  background: none;
  color: #42b983;
}
li {
  padding: 0.3rem 0;
  display:flex;
  gap: 5px;
}
li span {
  position: relative;
  color: #42b983;
  text-shadow: 0 0 5px black;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
li span:before {
  content: "";
  position: absolute;
  width: 0%;
  height: 2px;
  background: white;
  left: 0;
  top: 50%;
  transition: all 0.5s;
  transform: translate(0%, 50%);
  visibility: hidden;
}
li:hover span:before {
  width: 100%;
  visibility: unset;
}
li:hover span {
  opacity: 0.6;
}

li:hover button {
  visibility: unset;
}

li button {
  visibility: hidden;
  border: none;
  position: relative;
}
button:before {
  content: "";
  position: absolute;
}
</style>


